.App {
  text-align: center;
  border-color: #61dafb;
  border-width: 5px;
}

.App-clear{
  border: none;
  background: none;
  padding-left: 10px;
  padding-right: 80px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-links {
  color: rgb(6%, 94%, 98%);
  font-family: Arial, Helvetica, sans-serif;
}

.App-links:hover{
  color: white;
}

.dothing-outer{
  display: inline;
}

.dothing{
  color:white;
}

.dothing:hover{
  color:rgb(168, 240, 236);
}

.App-contacts{
  width: 100%;
  border-top: #ffffff;
  border-top-style: double;
  padding-top: 30px;
  color:  rgb(6%, 94%, 98%);
}

.App-lambda{
  height: 30px;
  padding-left: 25px;
  padding-right: 25px;
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.App-link {
  color: #61dafb;
}

